/* src/app/notfound/notfound.scss */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  font-family:
    "Inter",
    Arial,
    sans-serif;
  background: #f7f6f4;
  color: #212121;
  margin: 0;
  padding: 0;
}
:host {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  font-family:
    "Inter",
    Arial,
    sans-serif;
  background: #f7f6f4;
}
.ds-not-found {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 24px;
  box-shadow: 0 2px 8px rgba(33, 33, 33, 0.04);
  padding: 32px 24px;
  max-width: 600px;
  width: 100%;
  text-align: center;
  gap: 32px;
}
.ds-not-found__icon {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ececec;
  border-radius: 50%;
  width: 100px;
  height: 90px;
}
